
Template.__checkName("current_release");
Template["current_release"] = new Template("Template.current_release", (function() {
  var view = this;
  return Spacebars.With(function() {
    return Spacebars.call(view.lookup("current_release"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "content-wrapper"
    }, "\n      ", HTML.DIV({
      "class": "current-release grid-module grid banner"
    }, "\n        ", HTML.H4({
      "class": "banner-heading"
    }, Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "telescope_has_been_updated");
    })), "\n        ", HTML.DIV({
      "class": "markdown"
    }, "\n          ", Spacebars.include(view.lookupTemplate("markdown"), function() {
      return Blaze.View("lookup:notes", function() {
        return Spacebars.mustache(view.lookup("notes"));
      });
    }), "\n        "), "\n        ", HTML.A({
      href: "#",
      "class": "release-dismiss banner-dismiss"
    }, Blaze.View("lookup:icon", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("icon"), "close"));
    })), "\n      "), "\n    "), "\n  " ];
  });
}));
